@import '../../theme';

.inputWrapper {
    grid-area: input;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.inputFile {
    flex: 1;
    display: block;
    margin: 0 0 10px 0;
    border: 0;
    border-radius: 4px;
    padding: 12px;
    background-color: $color-enable;
    cursor: pointer;
    color: $secondary-text-color;

    &:hover {
        background-color: $color-enable-hover;
    }
}

.inputText {
    flex: 1;
    display: block;
    border: 1px solid $secondary-background-color;
    border-radius: 4px;
    padding: 11px;
    background-color: $primary-background-color;
    color: $primary-text-color;
}

@media screen and (min-width: 769px) {
    .inputWrapper {
        flex-direction: row;
    }
    .inputFile {
        align-self: stretch;
        margin: 0 10px 0 0;
    }
    .inputText {
        align-self: stretch;
    }
}