@import '../../theme';

.sideBar {
    display: flex;
    width: 34px;
    height: 34px;
    flex-direction: column;
    align-self: flex-start;
    background-color: $secondary-background-color;
    text-align: center;
    transition: 0.3s ease;

    &Expanded {
        width: 103px;
        height: 100%;
        align-self: stretch;
    }

    .buttonToggle {
        border: 0;
        padding: 2px;
        background-color: $secondary-background-color;
        font-weight: 600;
        color: $secondary-text-color;

        &:focus {
            outline: none;
        }

        &.expanded {
            transform: rotateZ(45deg);
        }
    }
}

.sideBarButtonGroup {
    display: flex;
    flex-direction: column
}

.sideBarLink {
    padding: 12px;
    color: $secondary-text-color;
    text-decoration: none;
}

.sideBarLinkActive {
    font-size: 16px;
    font-weight: 600;
    color: $secondary-text-color;
    text-decoration: underline;
}
