@import '../../theme';

.wrapper {
    display: grid;
    grid-gap: 10px;
    flex: 1;
    grid-template-columns: 1fr;
    grid-template-areas: 'header' 'input' 'display-toggle' 'info' 'tlog' 'alog';

    h1 {
        grid-area: header;
        font-size: 40px;
        text-align: center;
    }
}

@media screen and (min-width: 769px) {
    .wrapper {
        grid-template-columns: 1fr 2fr 2fr 1fr;
        grid-template-areas: 'header header header header' '. input input .' 'display-toggle display-toggle display-toggle display-toggle' '. info info .' '. tlog alog .';
    }
}