@import '../../theme';

.wrapper {
    grid-area: alog;
    display: flex;
    flex-direction: column;
}

.selector {
    margin-bottom: 10px;
    border: 1px solid $secondary-background-color;
    border-radius: 4px;
    padding: 5px;
    background-color: $primary-background-color;
    color: $primary-text-color;
}

.textarea {
    flex: 1;
    border: 1px solid $secondary-background-color;
    border-radius: 4px;
    padding: 5px;
    background-color: $primary-background-color;
    color: $primary-text-color;
}