@import '../../theme';

.textareaWrapper {
    flex: 1;
    display: flex;
    position: relative;
    align-items: stretch;
    border: 1px solid $secondary-background-color;
    border-radius: 4px;
}

.textarea {
    flex: 1;
    resize: none;
    border: 0;
    background-color: transparent;
    padding: 5px;
    color: $primary-text-color;

    &::-webkit-scrollbar {
        display: none;
    }

    &:active,
    &:focus {
        outline: none;
    }
}

.count {
    position: absolute;
    bottom: 0;
    right: 0;
    border-bottom-right-radius: 4px;
    padding: 5px;
    background-color: $primary-background-color;
    font-size: 12px;
    color: $third-text-color;
    z-index: 1;
}