@import '../../theme';

.wrapper {
    grid-area: display-toggle;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.buttonShow {
    margin: 2px;
    border-radius: 4px;
    border: 0;
    padding: 6px 9px;
    background-color: $color-enable;
    cursor: pointer;
    color: $secondary-text-color;

    &:hover {
        background-color: $color-enable-hover;
    }
}

.buttonHide {
    margin: 2px;
    border-radius: 4px;
    border: 0;
    padding: 6px 9px;
    background-color: $color-disable;
    cursor: pointer;
    color: $secondary-text-color;

    &:hover {
        background-color: $color-disable-hover;
    }
}